.upload-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .upload-form {
    max-width: 800px;
    padding: 50px;
    border: 2px solid #ccc;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  
  .upload-form label {
    display: flex;
    flex-direction: column;
    margin: 10px 0;
  }
  
  .upload-form label span {
    font-weight: bold;
  }
  
  .upload-form input[type="text"],
  .upload-form textarea,
  .upload-form input[type="file"] {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
  .upload-form button {
    padding: 10px 20px;
    background-color: #4caf9e;
    color: white;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    margin-top: 20px;
  }
  
  .upload-form button:hover {
    background-color: #3e8e7f;
  }
  